body {
  font-family: Tahoma, Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 13px;
  background: #eeeeee;
  -webkit-font-smoothing: antialiased; }
.wrapper {
  max-width: 100%; }
.inner {
  max-width: 800px;
  margin: 0px auto;
  padding: 20px 10px; }
header {
  max-width: 1100px;
  margin: 0 auto;
  width: 100%;
  height: 45px;
  background: #474c61; }
header .inner {
    margin: 0 auto;
    padding: 0 10px;
    position: relative;
    z-index: 1005; }
header .inner .responsive-menu {
      position: absolute;
      right: 10px;
      top: 10px; }
header .inner .accordion-menu .is-accordion-submenu a {
      padding: 10px; }
header .inner .accordion-menu ul {
      background: #1a8860;
      margin: 0px; }
header .inner .accordion-menu li {
      border-bottom: 1px solid #8a8a8a; }
header .inner .accordion-menu li a:after {
        border-color: #ece9cf transparent transparent !important; }
header .inner .accordion-menu li a {
        padding: 10px; }
header .inner .accordion-menu li:last-child {
      border-bottom: 0px solid #8a8a8a; }
header .inner .top-bar .dropdown li.is-dropdown-submenu-parent > a::after {
      border-color: #84a297 transparent transparent; }
header .inner .top-bar .dropdown .submenu {
      margin-top: 0px;
      border: 0; }
header .inner .top-bar .dropdown .submenu a {
        color: #fff;
        text-transform: none; }
header .inner .top-bar .dropdown .submenu a :hover {
          color: #ffffff; }
header .inner .top-bar a {
      color: #ece9cf;
      font-family: Arial, Tahoma, sans-serif;
      font-size: 10px;
      text-transform: uppercase; }
header .logo {
    height: 44px;
    width: 160px;
    float: left;
    background: #474c61;
    position: relative; }
header .logo a {
      color: #fefefe !important;
      font-size: 23px !important;
      margin-left: 15px;
      position: absolute;
      bottom: 7px;
      padding: 0;
      text-transform: unset !important;
      font-family: 'Pacifico', cursive !important; }
header .logo a span {
        color: #3de688;
        font-size: 11px; }
main {
  max-width: 1100px;
  margin: 0 auto;
  background: #fff; }
main .inner {
    padding: 30px 10px 60px; }
main .inner .site-title {
      position: relative; }
main .inner .site-title .main {
        background: #84a719;
        color: white;
        padding: 0 10px;
        border-radius: 2px;
        font-size: 35px; }
main .inner .site-title .sub {
        background: #84a719;
        font-size: 13px;
        color: #e4ff03;
        position: absolute;
        bottom: -11px;
        left: 64%;
        z-index: 0;
        padding: 0 7px; }
main section .label {
    margin-bottom: 3.3333px; }
main section .button-group {
    display: block; }
main aside .first {
    margin-top: 57px; }
footer .inner .credit {
  text-align: center; }
footer .inner .menu li {
  margin: 0 24px; }
footer .inner .menu li a {
    line-height: 1.5; }
.top-button {
  position: fixed;
  bottom: 10px;
  right: 20px;
  font-size: 50px;
  color: #82ecc6; }
.clickable {
  cursor: pointer !important; }
.input-group {
  margin-bottom: 10px !important; }
.default.label {
  background: #ffffff;
  color: #0a0a0a; }
.margin-bottom-10 {
  margin-bottom: 10px; }
.account-information {
  margin-bottom: 20px; }
@media only screen and (max-width: 640px) {
  header .inner {
    padding-right: 0px;
    padding-left: 0px; } }
.compare-search {
  margin: 30px 15px;
  background: #474b60;
  padding: 20px 20px 1px;
  border-radius: 3px; }
.compare-search .first {
    border-top-left-radius: 3px !important;
